<template>
  <div>
    <div class="p3">
      <a class="tertiary font-title" @click="signOut"><i class="fas fa-sign-out-alt fa-rotate-180 m1-right"></i>EXIT</a>
    </div>

    <div class="p3 p1-top">
      <div v-if="property" class="m4-bottom">
        <p class="font-title light font3">{{property.description}}</p>
        <p class="tertiary line180">{{property.city}}, {{property.state}} ({{property.county}})</p>
      </div>

      <title-label title="forms" class="m4-top"></title-label>
      <div class="m4-bottom flex-row p2-left p2-right">
        <div class="form-item flex-one border-base2 m2-right text-center">
          <p class="form-title font2 bold font-title p1 border-bottom-base2"><i class="fas fa-poll-h m1-right"></i>TDS</p>
          <p class="p2" v-if="!tds"><a @click="fillForm('tds')">FILL FORM</a></p>
          <div v-if="tds">
            <p class="p2"><a @click="showPdf(tds.Key)">VIEW</a></p>
            <div class="p2-h"><p class="border-base2"></p></div>
            <p class="p2"><a @click="$router.push('/disclosures?form=tds')">EDIT</a></p>
          </div>
        </div>

        <div class="form-item flex-one border-base2 text-center">
          <p class="form-title font2 bold font-title p1 border-bottom-base2"><i class="fas fa-poll-h m1-right"></i>SSC</p>
          <p class="p2" v-if="!ssc"><a @click="fillForm('ssc')">FILL FORM</a></p>
          <div v-if="ssc">
            <p class="p2"><a @click="showPdf(ssc.Key)">VIEW</a></p>
            <div class="p2-h"><p class="border-base2"></p></div>
            <p class="p2"><a @click="$router.push('/disclosures?form=ssc')">EDIT</a></p>
          </div>
        </div>
      </div>

      <div class="appointments">
        <title-label title="Appointments"></title-label>
        <div v-for="appointment in appointments" :key="appointment.id" class="appointment-wrapper" :class="[appointment.status]">
          <h3 class="uppercase tertiary2 m4-top m1-bottom font-title"><span>{{appointment.status}}</span></h3>
          <appointment :appointment="appointment" :comments="apptComments(appointment.id)" class="m2-bottom" @change="apptChanged" @comment="commentAdded"></appointment>
        </div>
        <p v-if="appointments.length === 0" class="tertiary">No appointments</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import TitleLabel from '@/components/TitleLabel'
import Appointment from '@/views/Appointment'
import sort from 'fast-sort'
import moment from 'moment'

export default {
  name: 'Client',
  components: { TitleLabel, Appointment },
  data () {
    return {
      appointments: [],
      property: undefined,
      files: [],
      comments: []
    }
  },
  props: ['clientId'],
  inject: ['userClient', 'apptHelper', 'commentsClient'],
  computed: {
    ...mapGetters(['session']),
    tds () {
      return this.files.find(f => f.Key.includes('tds.pdf'))
    },
    ssc () {
      return this.files.find(f => f.Key.includes('ssc.pdf'))
    }
  },
  methods: {
    ...mapActions(['setSession', 'setPdf']),
    signOut () {
      this.setSession(undefined)
      this.$router.push('/')
    },
    showPdf (key) {
      this.setPdf(this.userClient.getFileUrl(this.clientId, key))
      this.$router.push(`/pdf`)
    },
    fetchAppointments () {
      this.userClient.getAppointments(this.clientId)
        .then(appointments => {
          this.appointments = appointments
          this.apptHelper.sortAppointments(this.appointments)
        })
    },
    fetchProperty () {
      this.userClient.getProperty(this.clientId)
        .then(property => {
          this.property = property
        })
    },
    fetchFiles () {
      this.userClient.getFiles(this.clientId)
        .then(files => {
          this.files = files
        })
    },
    async fetchComments () {
      this.comments = await this.commentsClient.getComments(this.clientId)
      sort(this.comments).desc('createdat')
    },
    fillForm (form) {
      let m = moment(new Date())
      const prepopulate = {
        property_city: this.property.city,
        property_county: this.property.county,
        property_name: this.property.description,
        property_described_as: this.property.description,
        property_as_of_date: m.format('MM/DD'),
        property_as_of_year: m.format('YYYY'),
        property_date: m.format('MM/DD/YYYY')
      }
      const state = encodeURIComponent(JSON.stringify(prepopulate))
      this.$router.push(`/disclosures?form=${form}&state=${state}`)
    },
    apptComments (appointmentId) {
      return this.apptHelper.findComments(appointmentId, this.comments)
    },
    commentAdded (comment) {
      this.comments.push(comment)
      sort(this.comments).desc('createdat')
    },
    apptChanged (updatedAppointment) {
      const index = this.appointments.findIndex(a => a.id === updatedAppointment.id)
      this.appointments[index] = updatedAppointment
      this.apptHelper.sortAppointments(this.appointments)
    }
  },
  mounted () {
    this.fetchAppointments()
    this.fetchProperty()
    this.fetchFiles()
    this.fetchComments()
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/sass/app";

.form-item {
  border-top-left-radius: 10px;
  overflow: hidden;
  .form-title {
    background-image: linear-gradient(45deg,rgba($tertiary, 0.2), rgba($base-color2, 0.5), rgba($primary, 0.2))
  }
}
.appointments {
  .pending h3 span { border-bottom: solid 1px #24c1ff; }
  .scheduled h3 span { border-bottom: solid 1px #0461c2; }
  .completed h3 span { border-bottom: solid 1px #767569; }
  .canceled h3 span { border-bottom: solid 1px #fc0000; }
  .declined h3 span { border-bottom: solid 1px #ff9d10; }
  .pending ~ .pending, .scheduled ~ .scheduled, .declined ~ .declined, .completed ~ .completed, .canceled ~ .canceled {
    h3 {
      display: none;
    }
  }
}
</style>
