<template>
  <div class="flex-column">
    <div class="p3">
      <a class="tertiary font-title" @click="$router.push('/')"><i class="fas fa-times m1-right"></i>CANCEL</a>
    </div>
    <form class="flex-one flex-column flex-center p3 p5-bottom" @submit.prevent="login">
      <div class="m3-bottom">
        <p class="font-title font2 secondary m3-bottom">USERNAME</p>
        <input type="email" class="input input-tertiary" autofocus="true" v-model="email">
      </div>

      <div class="m3-bottom">
        <p class="font-title font2 secondary m3-bottom">PASSWORD</p>
        <input type="password" class="input input-tertiary" v-model="password">
      </div>

      <div class="flex-row">
        <div class="flex-one"></div>
        <button class="bg-clear p2 secondary border-secondary circle"><i class="fas fa-arrow-right"></i></button>
      </div>
      <p v-if="error" class="error"><i class="fas fa-exclamation-circle m1-right"></i>Unable to enter</p>
    </form>
    <div class="p3">
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'Login',
  data () {
    return {
      email: undefined,
      password: undefined,
      error: undefined
    }
  },
  inject: ['loginClient'],
  methods: {
    ...mapActions(['setSession']),
    login () {
      const self = this
      this.loginClient.login(this.email, this.password)
        .then(session => {
          self.setSession(session)
          self.$router.push(session.role === 'realtor' ? '/realtor' : `/client/${session.id}`)
        })
        .catch(e => {
          self.error = e.message
        })
    }
  }
}
</script>
