<template>
  <div class="appointment-detail fixed cover z3 flex-column">
    <div class="flex-one flex-column bg-base shadow-top p3">
      <div class="flex-row align-center tertiary m2-bottom">
        <h2 class="font-title font-2 uppercase">{{appointment.status}}</h2>
        <div class="flex-one"></div>
        <a @click="close"><i class="fa fa-times"></i></a>
      </div>

      <div class="m2-bottom" :class="[appointment.status]"></div>

      <div class="flex-one scrolly">
        <div class="flex-row align-center m2-bottom relative" @click="focus('appt-label')">
          <h3 class="font-2 tertiary font-title m1-right uppercase">Label:</h3>
          <input type="text" class="flex-one bg-clear contrast app-font no-border p1 border-bottom-base2" placeholder="no label" name="appt-label" v-model="apptLabel">
        </div>

        <div class="flex-row align-center m2-bottom relative" @click="focus('appt-day')">
          <h3 class="font-2 tertiary font-title m1-right">DAY:</h3>
          <input type="date" class="hidden-input absolute" name="appt-day" v-model="apptDate">
          <p class="p1 border-bottom-base2 uppercase">{{time.getMonthDay(apptDate)}}, <span class="font-3">{{time.getDayOfWeek(apptDate)}}</span></p>
        </div>

        <div class="flex-row align-center m2-bottom relative" @click="focus('appt-time')">
          <h3 class="font-2 tertiary font-title m1-right">TIME:</h3>
          <input type="time" class="hidden-input absolute" name="appt-time" v-model="apptTime">
          <p class="p1 border-bottom-base2 uppercase">{{time.formatTime(apptTime)}}</p>
        </div>

        <div class="m4-top m4-bottom">
          <div class="flex-row flex-wrap">
            <div v-if="showDelete" class="flex-column decline-button border-base2 p1 rounded text-center" @click="deleteAppointment">
              <div class="m1-bottom"><i class="fas fa-trash"></i></div>
              <p class="font-5">DELETE</p>
            </div>
            <div v-if="showCancel" class="flex-column decline-button border-base2 p1 rounded text-center" @click="updateAppointment({status: 'canceled'})">
              <div class="m1-bottom"><i class="fas fa-times"></i></div>
              <p class="font-5">CANCEL</p>
            </div>

            <div v-if="showDelete || showCancel" class="m3"></div>

            <div v-if="showGetFeedback" class="flex-column secondary border-base2 p1 rounded text-center" @click="requestFeedback">
              <p class="font-5 uppercase line140">Request</p>
              <p class="font-5 uppercase">Feedback</p>
            </div>

            <div v-if="showReschedule" class="flex-column secondary border-base2 p1 rounded text-center" @click="showReschedulePrompt = true">
              <div class="m1-bottom"><i class="fas fa-check" data-fa-transform="shrink-3"></i></div>
              <p class="font-5">RESCHEDULE</p>
            </div>
          </div>
        </div>

        <div :class="{pulse: loadingComments}">
          <h3 class="tertiary font-title font-3 m4-top m2-bottom p1-bottom border-bottom-base2 uppercase">Activity</h3>

          <form class="flex-row m2-top m2-bottom" @submit.prevent="addComment">
            <input class="input input-tertiary flex-one rounded" placeholder="comment..." v-model="newComment">
            <button class="contrast bg-clear no-border rounded p2-left p2-right border-bottom-primary border-right-primary"><i class="fas fa-arrow-right"></i></button>
          </form>

          <div v-if="hasComments">
            <div v-for="comment in comments" :key="comment.id" class="flex-row align-center p1-bottom">
              <div class="bg-sheen border-base2 p1 p2-left p2-right rounded3">
                <!-- <p class="line140"><span class="secondary uppercase font-3">{{time.formatFull(comment.createdat)}}</span></p> -->
                <p>{{comment.message}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <transition name="fade-in">
        <div v-if="hasEdits">
          <button class="button alt h-fill uppercase" @click="saveChanges">Save</button>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import appointmentMixins from '@/js/mixins/appointment-mixins'

export default {
  name: 'AppointmentDetail',
  data () {
    return {
      apptDate: undefined,
      apptTime: undefined,
      apptLabel: undefined,
      newComment: undefined
    }
  },
  mixins: [appointmentMixins],
  props: ['appointment'],
  computed: {
    timeChanged () {
      return this.apptDate !== this.appointment.date ||
        this.apptTime !== this.appointment.time
    },
    hasEdits () {
      return this.apptLabel !== this.appointment.label ||
        this.timeChanged
    }
  },
  methods: {
    close () {
      this.$emit('done')
    },
    focus (name) {
      if (this.mode !== 'realtor') return
      this.$el.querySelector(`input[name="${name}"]`).focus()
    },
    requestFeedback () {
      this.submitComment('Feedback Requested', [{ type: 'tag', tag: 'FEEDBACK' }])
    },
    async addComment () {
      await this.submitComment(this.newComment)
      this.newComment = undefined
    },
    saveChanges () {
      const status = this.timeChanged ? 'scheduled' : this.appointment.status
      this.updateAppointment({ status, date: this.apptDate, time: this.apptTime, label: this.apptLabel })
    }
  },
  mounted () {
    this.apptDate = this.appointment.date
    this.apptTime = this.appointment.time
    this.apptLabel = this.appointment.label
  }
}
</script>

