  button { color: red; }
  color: red;
}
.appt-title {
  background-image: linear-gradient(45deg,rgba($tertiary, 0.2), rgba($base-color2, 0.5), rgba($primary, 0.2))
}
.pending { border-bottom: solid 2px #24c1ff; }
.scheduled { border-bottom: solid 2px #0461c2; }
.completed { border-bottom: solid 2px #767569; }
.canceled { border-bottom: solid 2px #fc0000; }
