const url = process.env.VUE_APP_REALTORS_URL

export default {
  async getClients (session) {
    const options = {
      headers: {
        'token': btoa(session.id)
      }
    }
    return fetch(`${url}/realtors/clients`, options)
      .then(responseAsJson)
  },
  async createClient (clientName, clientEmail, clientPassword, clientIsRealtor, property) {
    const body = { email: clientEmail, password: btoa(clientPassword), name: clientName, role: clientIsRealtor ? 'realtor' : 'client', property }
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json; charset=utf-8'
      },
      body: JSON.stringify(body)
    }
    return fetch(`${url}/users`, options)
      .then(responseAsJson)
  },
  async closeUserAccount (userId, session) {
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
        'token': btoa(session.id)
      },
      body: JSON.stringify({ status: 'closed' })
    }
    return fetch(`${url}/realtors/clients/${userId}`, options)
      .then(responseAsJson)
  },
  async deleteUser (userId, session) {
    const options = {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
        'token': btoa(session.id)
      }
    }
    return fetch(`${url}/realtors/clients/${userId}`, options)
      .then(responseAsJson)
  }
}

function responseAsJson (response) {
  if (response.status >= 400) {
    throw new Error('Error')
  }
  return response.json()
}
