<template>
  <div class="radio-prompt">
    <button v-for="(option, i) in prompt.options"
      :key="option.key"
      class="button alt contrast shadow"
      :class="{selected: responses[option.key] === 'Yes'}"
      @click="done(option)">{{promptText(option, i)}}</button>
  </div>
</template>

<script>
export default {
  name: 'RadioPrompt',
  props: ['prompt', 'responses'],
  methods: {
    promptText (option, index) {
      return option.prompt || (this.prompt.options.length !== 2 ? index : index === 0 ? 'Yes' : 'No')
    },
    compClasses (key) {
      return this.responses[key] === 'Yes' ? ['selected'] : []
    },
    done (option) {
      let responseValues = {}
      this.prompt.options.forEach(o => (responseValues[o.key] = ''))
      responseValues[option.key] = 'Yes'
      this.$emit('done', responseValues)
      this.$emit('change', responseValues)
    }
  }
}
</script>

<style lang="scss">
