.radio-prompt {
  display: flex;
  flex-direction: column;
  padding: 16px;
  button {
    margin-bottom: 16px;
  }
  button.selected {
    background: linear-gradient(90deg, $primary, transparent, $primary);
  }
}
.group-prompt .radio-prompt {
  display: flex;
  flex-direction: row;
  padding: 8px;
  button {
    flex: 1;
    margin-bottom: 0px;
    margin-right: 10px;
    padding: 8px;
  }
}
</style>
