<template>
  <div class="home">
    <div class="v-fill h-fill fixed z-2">
      <div class="banner v-fill z0"></div>
      <div class="vignette absolute cover z1"></div>
      <div class="absolute cover z2 flex-column">
        <p class="p2-left fade-down"><img class="logo" src="@/assets/images/logo.jpg"/></p>
        <div class="flex-one"></div>
        <div class="p1 p3-top fade-up">
          <p class="text-center text-shadow">LEARN MORE</p>
          <p class="text-center"><i class="fas fa-chevron-down font3"></i></p>
        </div>
      </div>
    </div>

    <div class="v-fill scrolly" v-on:scroll="scroll">
      <div class="v-fill z0 relative">
        <div class="fixed login-container text-center">
          <button class="button bg-primary contrast font-title shadow" @click="$router.push('/login')"><i class="fas fa-sign-in-alt m1-right"></i>ENTER</button>
        </div>
      </div>
      <div class="v-fill-min z1 relative bg-base shadow-top rounded3">
        <div class="p3 p5-top">
          <transition :name="transition">
            <about v-if="view === 'about'" :show="show"></about>
            <photography v-if="view === 'photography'" :show="show"></photography>
            <videography v-if="view === 'videography'" :show="show"></videography>
            <listings v-if="view === 'listings'" :show="show"></listings>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import About from '@/views/About'
import Photography from '@/views/Photography'
import Videography from '@/views/Videography'
import Listings from '@/views/Listings'

export default {
  name: 'home',
  data () {
    return {
      view: 'about',
      transition: 'slide-left'
    }
  },
  components: { About, Photography, Videography, Listings },
  methods: {
    scroll (event) {
      const banner = this.$el.querySelector('.banner')
      const scroll = event.target.scrollTop / event.target.offsetHeight
      banner.style.transform = `scale(${1 + scroll})`
      banner.style.filter = `blur(${scroll * 5}px)`
    },
    show (view) {
      const scrollView = this.$el.querySelector('.scrolly')
      if (scrollView.scrollTop > scrollView.offsetHeight) {
        // scrollView.scrollTop = scrollView.offsetHeight
        scrollView.scrollTo(0, scrollView.offsetHeight)
      }
      this.transition = view === 'about' ? 'slide-right' : 'slide-left'
      this.view = view
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/sass/colors";
.fade-down {
  background: linear-gradient($base-color, transparent);
}
.fade-up {
  background: linear-gradient(transparent 10%, rgba($base-color, 0.5));
}
.logo { max-height: 80px; }
.banner {
  background-image: url('../assets/images/banner-home.jpg');
  background-size: cover;
  background-position: center;
}
