<template>
  <div class="flex-column">
    <div class="p2 flex-row">
      <a class="tertiary font-title" @click="$router.back()"><i class="fas fa-chevron-left m1-right"></i>BACK</a>
      <div class="flex-one"></div>
      <div class="p1-bottom p1-left border-bottom-secondary">{{client.name}}</div>
      <a class="m1-left" :href="inviteLink">
        <span class="fa-layers fa-fw">
          <i class="fas fa-circle tertiary" data-fa-transform="grow-7"></i>
          <i class="far fa-envelope base" data-fa-transform=""></i>
        </span>
      </a>
    </div>

    <div class="flex-one scrolly">
      <div class="p3 p0-top">
        <div v-if="property" class="m4-bottom">
          <p class="font-title light font3">{{property.description}}</p>
          <p class="tertiary line180">{{property.city}}, {{property.state}} ({{property.county}})</p>
        </div>

        <button class="button alt h-fill m3-bottom" @click="createAppointmentRequest"><i class="fas fa-plus-circle m1-right"></i>ADD SHOWING</button>

        <div class="appointments">
          <title-label title="Appointments"></title-label>
          <div v-for="appointment in appointments" :key="appointment.id" class="appointment-wrapper" :class="[appointment.status]">
            <h3 class="uppercase tertiary2 m3-top m1-bottom font-title light"><span>{{appointment.status}}</span></h3>
            <appointment
              :appointment="appointment"
              :comments="apptComments(appointment.id)"
              mode="realtor"
              :clientId="clientId"
              class="m2-bottom"
              @change="apptChanged"
              @comment="commentAdded" />
          </div>
          <p v-if="appointments.length === 0" class="tertiary m4-top text-center font3">No appointments</p>
        </div>
      </div>

      <div v-if="files && files.length" class="p3">
        <title-label title="FILES"></title-label>
        <ul>
          <li v-for="(file, i) in files" :key="i" class="flex-row p2 m1-bottom border-base2" @click="showPdf(file.Key)">
            <p class="flex-one">{{file.Key}}</p>
            <i class="fas fa-chevron-right m1-right"></i>
          </li>
        </ul>
      </div>

      <div class="p3">
        <hr class="border-primary m5-top m5-bottom">
        <div class="m4-bottom">
          <button class="button alt uppercase" @click="closeUserAccount">Close Account</button>
        </div>

        <a class="primary m2-right" @click="deleteUser"><i class="fas fa-times m1-right primary"></i><value-transition :value="deletePrompt" name="fade" /></a>
        <transition class="fade">
          <button v-if="confirmingDelete" class="button alt m2-top" @click="cancelDelete">CANCEL</button>
        </transition>
      </div>
    </div>

    <transition name="slide-up">
      <div v-if="prompt" class="prompt fixed bg-base z2 shadow p3 flex-column">
        <div class="m4-bottom text-right">
          <a class="tertiary font-title" @click="prompt = false"><i class="fas fa-times m1-right"></i>CANCEL</a>
        </div>

        <create-appointment :client-id="clientId" :done="dismissPrompt" class="flex-one"></create-appointment>
      </div>
    </transition>
  </div>
</template>

<script>
import TitleLabel from '@/components/TitleLabel'
import Appointment from '@/views/Appointment'
import CreateAppointment from '@/views/forms/CreateAppointment'
import sort from 'fast-sort'
import { generateInviteLink } from '@/js/mail'
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'RealtorClient',
  data () {
    return {
      prompt: false,
      appointments: [],
      property: undefined,
      files: undefined,
      comments: [],
      confirmingDelete: false
    }
  },
  props: ['clientId'],
  computed: {
    ...mapGetters(['clients', 'session']),
    client () {
      return this.clients.find(c => c.id === parseInt(this.clientId))
    },
    inviteLink () {
      return generateInviteLink(this.client)
    },
    deletePrompt () {
      return this.confirmingDelete ? 'CONFIRM DELETE' : `DELETE ${this.client.name}`
    }
  },
  inject: ['userClient', 'realtorClient', 'commentsClient', 'apptHelper'],
  components: { TitleLabel, Appointment, CreateAppointment },
  methods: {
    ...mapActions(['setPdf']),
    fetchAppointments () {
      const self = this
      this.userClient.getAppointments(this.clientId)
        .then(appointments => {
          self.appointments = appointments
          this.apptHelper.sortAppointments(this.appointments)
        })
    },
    fetchProperty () {
      this.userClient.getProperty(this.clientId)
        .then(property => {
          this.property = property
        })
    },
    fetchFiles () {
      this.userClient.getFiles(this.clientId)
        .then(files => {
          this.files = files
        })
    },
    async fetchComments () {
      this.comments = await this.commentsClient.getComments(this.clientId)
      sort(this.comments).desc('createdat')
    },
    commentAdded (comment) {
      this.comments.push(comment)
      sort(this.comments).desc('createdat')
    },
    showPdf (key) {
      this.setPdf(this.userClient.getFileUrl(this.clientId, key))
      this.$router.push(`/pdf`)
    },
    createAppointmentRequest () {
      this.prompt = true
    },
    dismissPrompt () {
      this.fetchAppointments()
      this.prompt = false
    },
    closeUserAccount () {
      this.realtorClient.closeUserAccount(this.clientId, this.session)
        .then(r => (this.$router.push('/realtor')))
    },
    deleteUser () {
      if (this.confirmingDelete) {
        const self = this
        this.realtorClient.deleteUser(this.clientId, this.session)
          .then(r => { self.$router.push('/realtor') })
      } else {
        this.confirmingDelete = true
      }
    },
    cancelDelete () {
      this.confirmingDelete = false
    },
    apptComments (appointmentId) {
      return this.apptHelper.findComments(appointmentId, this.comments)
    },
    apptChanged (updatedAppointment) {
      const index = this.appointments.findIndex(a => a.id === updatedAppointment.id)
      if (updatedAppointment.status === 'deleted') {
        this.appointments.splice(index, 1)
      } else {
        this.appointments[index] = updatedAppointment
      }
      this.apptHelper.sortAppointments(this.appointments)
    }
  },
  mounted () {
    this.fetchAppointments()
    this.fetchProperty()
    this.fetchFiles()
    this.fetchComments()
  }
}
</script>

<style lang="scss" scoped>
.appointments {
  .pending h3 span { border-bottom: solid 1px #24c1ff; }
  .scheduled h3 span { border-bottom: solid 1px #0461c2; }
  .completed h3 span { border-bottom: solid 1px #767569; }
  .canceled h3 span { border-bottom: solid 1px #fc0000; }
  .declined h3 span { border-bottom: solid 1px #ff9d10; }
  .pending ~ .pending, .scheduled ~ .scheduled, .declined ~ .declined, .completed ~ .completed, .canceled ~ .canceled {
    h3 {
      display: none;
    }
  }
}
a.primary {
  text-shadow: 1px 0 7px black;
}
</style>
