const statusEnum = {
  pending: 10,
  declined: 20,
  scheduled: 30,
  completed: 40,
  canceled: 50
}

export default {
  sortAppointments (appointments) {
    appointments.sort((a, b) => {
      if (statusEnum[a.status] > statusEnum[b.status]) return 1
      if (statusEnum[a.status] < statusEnum[b.status]) return -1
      if (a.date > b.date) return 1
      if (a.date < b.date) return -1

      return 0
    })
  },
  findComments (appointmentId, comments) {
    return (comments || [])
      .filter(c => (c.tags || []).find(t => t.refType === 'appointment' && t.refId === appointmentId) !== undefined)
  }
}
