<template>
  <div class="flex-column">
    <title-label title="REQUEST SHOWING"></title-label>
    <form class="flex-one flex-column" @submit.prevent="createAppointment">
      <div>
        <label for="startDate" class="tertiary font-title font-2 m1-bottom">DATE</label>
        <input type="date" class="input input-tertiary m2-bottom" v-model="date">

        <label for="startTime" class="tertiary font-title font-2 m1-bottom">TIME</label>
        <input type="time" class="input input-tertiary m2-bottom" name="startTime" v-model="time">

        <label for="label" class="tertiary font-title font-2 m1-bottom">LABEL</label>
        <input type="text" class="input input-tertiary m2-bottom" name="label" v-model="label">
      </div>

      <div class="flex-one"></div>
      <button class="button alt h-fill" :disabled="isLoading">REQUEST<i v-if="isLoading" class="fas fa-spinner fa-spin m1-left"></i></button>
    </form>
  </div>
</template>

<script>
import TitleLabel from '@/components/TitleLabel'

export default {
  name: 'CreateAppointment',
  data () {
    return {
      date: undefined,
      time: undefined,
      label: undefined,
      isLoading: false,
      error: undefined
    }
  },
  props: ['clientId', 'done'],
  components: { TitleLabel },
  inject: ['userClient'],
  methods: {
    createAppointment () {
      this.isLoading = true
      const self = this
      this.userClient.createAppointment(this.clientId, this.date, this.time, 'scheduled', this.label)
        .then(r => {
          self.isLoading = false
          self.done()
        })
        .catch(e => {
          self.isLoading = false
          self.error = 'Error creating appointment'
        })
    }
  }
}
</script>
