<template>
  <div>
    <div class="flex-row m2-bottom">
        <div class="primary" @click="show('about')"><i class="fas fa-chevron-left p1-right"></i> <span>BACK</span></div>
        <div class="flex-one"></div>
        <div class="font-title">PROPERTIES</div>
    </div>

    <div class="flex-column m-2-left m-2-right">
      <div v-for="(listing, i) in listings" :key="i" class="listing flex-column m1-bottom" :style="{backgroundImage: listing.image}">
        <div class="flex-one"></div>
        <div class="details flex-column p1 text-shadow">
          <p class="font3 font-bold">{{listing.title}}</p>
          <p class="secondary">{{listing.price}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Listings',
  data () {
    return {
      listings: []
    }
  },
  props: ['show'],
  inject: ['listingsClient'],
  mounted () {
    const self = this
    this.listingsClient.getListings()
      .then(listings => {
        self.listings = listings
      })
  }
}
</script>

