<template>
  <div class="appointment flex-column rounded" :class="{'fade-in-out': isLoading}">
    <div class="rounded border-base2 z1 shadow">
      <div class="p1 text-center appt-title relative" :class="[appointment.status]" @click="showDetails = true">
        <p class="font-title">{{month}} {{day}}</p>
        <p class="font-2 line180 tertiary2">{{appointment.label}}</p>
        <div class="absolute top-right primary"><i class="fas fa-ellipsis-h"></i></div>
      </div>

      <div class="flex-row align-center gradient-column">
        <div class="flex-one v-fill">
          <div v-if="showDelete" class="flex-column decline-button text-center" @click="deleteAppointment">
            <div class="m1-bottom"><i class="fas fa-trash"></i></div>
            <p class="font-5">DELETE</p>
          </div>
          <div v-if="showCancel" class="flex-column decline-button text-center" @click="updateAppointment({status: 'canceled'})">
            <div class="m1-bottom"><i class="fas fa-times"></i></div>
            <p class="font-5">CANCEL</p>
          </div>
        </div>

        <div class="flex-two flex-column align-center">
          <div class="flex-one font2 p1">{{displayTime}}</div>
          <div class="font-3 tertiary p1-bottom">{{dayOfWeek}}</div>
        </div>

        <div class="flex-one">
          <div v-if="showReschedule" class="flex-column secondary text-center" @click="showReschedulePrompt = true">
            <div class="m1-bottom"><i class="fas fa-check" data-fa-transform="shrink-3"></i></div>
            <p class="font-5">RESCHEDULE</p>
          </div>

          <div v-if="showEdit" class="flex-column secondary text-center" @click="showDetails = true">
            <div class="m1-bottom"><i class="far fa-edit" data-fa-transform="shrink-3"></i></div>
            <p class="font-5">EDIT</p>
          </div>
        </div>
      </div>
    </div>

    <div class="p1-left p1-right">
      <div v-if="hasComments" class="gradient-column border-base2 rounded opacity60 p1 p2-top">
        <p>
          <span class="rounded2 opacity70 bg-tertiary base font-2 bold m1-right p1-left p1-right">{{comments.length}}</span>
          <span class="font-2">... {{lastComment.message}}</span>
        </p>
      </div>
    </div>

    <transition name="fade-in">
      <reschedule-appointment v-if="showReschedulePrompt" @cancel="showReschedulePrompt = false" @done="requestReschedule"></reschedule-appointment>
    </transition>

    <transition name="slide-up">
      <appointment-detail
        v-if="showDetails"
        :appointment="appointment"
        :comments="comments"
        :mode="mode"
        :clientId="clientId"
        @change="updatedFromDetails"
        @comment="commentAdded"
        @done="showDetails = false" />
    </transition>
  </div>
</template>

<script>
import appointmentMixins from '@/js/mixins/appointment-mixins'
import RescheduleAppointment from '@/views/forms/RescheduleAppointment'
import AppointmentDetail from '@/views/AppointmentDetail'

export default {
  name: 'Appointment',
  mixins: [appointmentMixins],
  data () {
    return {
      showDetails: false
    }
  },
  computed: {
    lastComment () {
      return this.comments[0]
    },
    showEdit () {
      return this.mode === 'realtor' && ['pending', 'scheduled'].includes(this.appointment.status)
    }
  },
  components: { RescheduleAppointment, AppointmentDetail },
  methods: {
    updatedFromDetails (appointment) {
      this.$emit('change', appointment)
    },
    commentAdded (comment) {
      this.$emit('comment', comment)
    },
    editAppointment () {
      this.showDetails = true
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/sass/app";

.appointment {
  overflow: hidden;
  .top-right {
    top: 8px;
    right: 10px;
  }
  .decline-button {
    button { color: red; }
    color: red;
  }
  .appt-title {
    background-image: linear-gradient(45deg,rgba($tertiary, 0.2), rgba($base-color2, 0.5), rgba($primary, 0.2))
  }
  .appt-title.pending { border-bottom: solid 2px #24c1ff; }
  .appt-title.scheduled { border-bottom: solid 2px #0461c2; }
  .appt-title.completed { border-bottom: solid 2px #767569; }
  .appt-title.canceled { border-bottom: solid 2px #fc0000; }
  .appt-title.declined { border-bottom: solid 2px #ff9d10; }
}
</style>
