<template>
  <div class="reschedule-prompt fixed cover z3 flex-column flex-center align-center p1">
    <div class="bg-base p3 rounded shadow border-top-primary">
      <p class="secondary m2-bottom font-title uppercase">Request new time</p>
      <div class="m4-bottom">
        <input class="input input-tertiary" placeholder="comments, new time, etc." v-model="suggestedTime">
      </div>
      <div class="flex-row align-bottom">
        <a class="tertiary m2-right" @click="cancel">cancel</a>
        <div class="flex-one"></div>
        <button class="button alt" @click="reschedule">RESCHEDULE</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RescheduleAppointment',
  data () {
    return {
      suggestedTime: undefined
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
    },
    reschedule () {
      this.$emit('done', this.suggestedTime)
    }
  }
}
</script>

