<template>
  <div>
    <div class="p3 flex-row">
      <a class="tertiary font-title" @click="signOut"><i class="fas fa-sign-out-alt fa-rotate-180 m1-right"></i>EXIT</a>
      <div class="flex-one"></div>
      <button class="bg-clear contrast rounded3 p1 border-primary" @click="addClient"><i class="fas fa-plus m1-right"></i>CLIENT</button>
    </div>

    <div class="p3 p1-top">
      <title-label title="CLIENTS"></title-label>
      <div v-for="client in openClients" :key="client.id" class="p2-top p2-bottom border-bottom-base2" @click="viewClient(client)">
        <p>{{ client.name }}</p>
      </div>
      <p v-if="hasNoClients" class="tertiary">No active clients</p>

      <div class="m5-top">
        <title-label v-if="closedClients.length" title="CLOSED"></title-label>
        <div v-for="client in closedClients" :key="client.id" class="p2-top p2-bottom border-bottom-base2">
          <p class="tertiary">{{ client.name }}</p>
        </div>
      </div>
    </div>

    <transition name="slide-up">
      <div v-if="prompt" class="prompt fixed scrolly bg-base z2 shadow p3 flex-column">
        <div class="m4-bottom text-right">
          <a class="tertiary font-title" @click="prompt = false"><i class="fas fa-times m1-right"></i>CANCEL</a>
        </div>

        <create-client class="flex-one" @done="clientCreated"></create-client>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import TitleLabel from '@/components/TitleLabel'
import CreateClient from '@/views/forms/CreateClient'

export default {
  name: 'Realtor',
  components: { TitleLabel, CreateClient },
  data () {
    return {
      prompt: false
    }
  },
  inject: ['realtorClient'],
  computed: {
    ...mapGetters(['session', 'clients']),
    hasNoClients () {
      return this.clients.length === 0
    },
    openClients () {
      return this.clients.filter(c => c.status !== 'closed')
    },
    closedClients () {
      return this.clients.filter(c => c.status === 'closed')
    }
  },
  methods: {
    ...mapActions(['setSession', 'setClients']),
    signOut () {
      this.setSession(undefined)
      this.$router.push('/')
    },
    addClient () {
      this.prompt = true
    },
    viewClient (client) {
      this.$router.push(`/realtor/client/${client.id}`)
    },
    clientCreated () {
      this.prompt = false
      this.refreshClients()
    },
    refreshClients () {
      this.realtorClient.getClients(this.session)
        .then(clients => {
          this.setClients(clients)
        })
    }
  },
  mounted () {
    this.refreshClients()
  }
}
</script>

