<template>
  <form @submit.prevent="done">
    <div class="flex-row align-center">
      <p class="tertiary font1 m1-right">{{prompt.prefix}}</p>
      <input :type="kind" :placeholder="placeholder" :pattern="pattern" v-model="fieldValue" :maxlength="prompt.maxLength" autofocus="true" class="input input-tertiary flex-one" />
      <p class="tertiary font2 m1-left">{{prompt.suffix}}</p>
    </div>
    <p v-if="prompt.maxLength" class="text-right tertiary font-3 p1">{{charactersRemaining}}</p>
    <button v-if="showDoneButton" class="button alt h-fill m5-top" :disabled="!fieldValue">NEXT <i class="fas fa-arrow-right m1-left"></i></button>
  </form>
</template>

<script>
export default {
  name: 'TextPrompt',
  data () {
    return {
      fieldValue: undefined
    }
  },
  props: ['prompt', 'responses'],
  computed: {
    kind () {
      return this.prompt.kind || 'text'
    },
    responsesValue () {
      return this.responses[this.prompt.key]
    },
    pattern () {
      return this.prompt.kind === 'number' ? '[0-9]*' : undefined
    },
    placeholder () {
      return this.prompt.placeholder || 'type here'
    },
    charactersRemaining () {
      return this.prompt.maxLength
        ? `${this.fieldValue ? this.fieldValue.length : 0}/${this.prompt.maxLength}`
        : ''
    },
    showDoneButton () {
      return !['GroupPrompt', 'OptionsPrompt'].includes(this.$parent.$options.name)
    }
  },
  watch: {
    fieldValue () {
      this.$emit('change', { [this.prompt.key]: this.fieldValue })
    },
    responsesValue () {
      this.fieldValue = this.responsesValue
    },
    prompt () {
      this.fieldValue = this.responsesValue
    }
  },
  methods: {
    clear () {
      this.fieldValue = undefined
    },
    done () {
      this.$emit('done', { [this.prompt.key]: this.fieldValue })
    }
  },
  mounted () {
    this.fieldValue = this.responsesValue
  }
}
</script>

