<template>
  <div class="group-prompt">
    <div class="border-top-tertiary p1-top" :class="{'flex-row': prompt.orientation === 'h'}">
      <div v-for="(prompt, i) in prompt.items" :key="i" class="m4-bottom">
        <transition name="fade-in">
          <div v-if="checkCondition(prompt)">
            <h2 class="app-font light font2 m2-bottom">{{prompt.prompt}}</h2>
            <div :is="getComponent(prompt)" :prompt="prompt" :responses="responses" @change="promptFilled"></div>
          </div>
        </transition>
      </div>
    </div>
    <button v-if ="showDoneButton" class="button alt h-fill m5-top" @click="done">NEXT <i class="fas fa-arrow-right m1-left"></i></button>
  </div>
</template>

<script>
import promptComponents from '@/components/prompts/prompt-components'

export default {
  name: 'GroupPrompt',
  data () {
    return {
      formResponses: {}
    }
  },
  computed: {
    showDoneButton () {
      return !['GroupPrompt', 'OptionsPrompt'].includes(this.$parent.$options.name)
    }
  },
  props: ['prompt', 'responses'],
  methods: {
    getComponent (prompt) {
      return promptComponents[prompt.type]
    },
    checkCondition (prompt) {
      return !prompt.condition || prompt.condition(this.responses)
    },
    promptFilled (responseValues) {
      for (const key in responseValues) {
        this.$set(this.formResponses, key, responseValues[key])
      }
      this.$emit('change', this.formResponses)
    },
    done () {
      this.$emit('done', this.formResponses)
      this.$children.forEach(c => {
        if (c.clear) {
          c.clear()
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../assets/sass/app";

.group-prompt > .flex-row > div {
  flex: 1;
  &:first-child {
    border-right: solid 1px $base-color2;
    margin-right: 8px;
  }
  h2 {
    color: $tertiary;
  }
}
</style>
