<template>
  <div>
    <div class="bg-base2 p2 flex-row">
      <a class="uppercase" @click="close"><i class="fas fa-chevron-left m1-right"></i>back</a>
      <div class="flex-one"></div>
      <div class="flex-row align-center">
        <a class="m1-right" @click="back" :disabled="page === 1"><i class="fas fa-chevron-left"></i></a>
        <p class="tertiary font-2">Page {{page}}</p>
        <a class="m1-left" @click="forward" :disabled="page === pages"><i class="fas fa-chevron-right"></i></a>
      </div>
    </div>
    <pdf :src="pdf" :page="page" @num-pages="setNumberOfPages"></pdf>
  </div>
</template>

<script>
import Pdf from 'vue-pdf'
import { mapGetters } from 'vuex'

export default {
  name: 'PdfViewer',
  data () {
    return {
      page: 1,
      pages: 0
    }
  },
  computed: {
    ...mapGetters(['pdf'])
  },
  components: { Pdf },
  methods: {
    setNumberOfPages (n) {
      this.pages = n
    },
    close () {
      this.$router.back()
    },
    back () {
      if (this.page === 1) return
      this.page--
    },
    forward () {
      if (this.page === this.pages) return
      this.page++
    }
  }
}
</script>
