const generateInviteLink = (client) => {
  const subject = encodeURIComponent(`Invitation: Gullixson Real Estate app`)
  const message = encodeURIComponent(`Hi ${client.name},

Please download our Gullixson Team App in the Apple App store. The first thing you can do on the app is fill out a few of the important disclosure documents for your property. You can also use the app to accept or decline appointments and review any feedback on appointments at your home. This may include property inspections, pre-listing appointments (ie. stagers) and eventually property showings to buyers.

Regards,

The Gullixson Team`)

  return `mailto:${client.email}?subject=${subject}&body=${message}`
}

export { generateInviteLink }
