import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store'
import Home from './views/Home'
import Login from './views/Login'
import Realtor from './views/Realtor'
import RealtorClient from './views/RealtorClient'
import Client from './views/Client'
import Disclosures from './views/Disclosures'
import PdfViewer from './views/PdfViewer'
import Photography from './views/Photography'
import Videography from './views/Videography'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/realtor',
      name: 'realtor',
      component: Realtor,
      beforeEnter: realtorGuard
    },
    {
      path: '/realtor/client/:clientId',
      name: 'realtor-client',
      component: RealtorClient,
      props: true,
      beforeEnter: realtorGuard
    },
    {
      path: '/client/:clientId',
      name: 'client',
      component: Client,
      props: true,
      beforeEnter: clientGuard
    },
    {
      path: '/disclosures',
      name: 'disclosures',
      component: Disclosures,
      beforeEnter: clientGuard
    },
    {
      path: '/pdf',
      name: 'pdf',
      component: PdfViewer,
      beforeEnter: sessionGuard
    },
    {
      path: '/photography',
      name: 'photography',
      component: Photography
    },
    {
      path: '/videography',
      name: 'videography',
      component: Videography
    }
  ]
})

function sessionGuard (to, from, next) {
  if ({ ...store.state.session } === undefined) {
    next('/')
  } else {
    next()
  }
}

function realtorGuard (to, from, next) {
  if ({ ...store.state.session }.role !== 'realtor') {
    next('/')
  } else {
    next()
  }
}

function clientGuard (to, from, next) {
  if (!['client', 'realtor'].includes({ ...store.state.session }.role)) {
    next('/')
  } else {
    next()
  }
}
