const url = process.env.VUE_APP_USERS_URL

export default {
  async getComments (userId) {
    return fetch(`${url}/comments/${userId}`)
      .then(responseAsJson)
  },
  async createComment (comment) {
    const options = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(comment)
    }
    return fetch(`${url}/comments`, options)
      .then(responseAsJson)
  }
}

function responseAsJson (response) {
  if (response.status >= 400) {
    throw new Error('Error')
  }
  return response.json()
}
