<template>
  <div class="bool-prompt">
    <button
      class="button alt shadow"
      :class="{selected: responses[prompt.key] === 'Yes'}"
      @click="done('Yes')">YES</button>
      
    <button
      class="button alt shadow"
      :class="{selected: responses[prompt.key] === ''}"
      @click="done('')">NO</button>
  </div>
</template>

<script>
export default {
  name: 'BoolPrompt',
  props: ['prompt', 'responses'],
  methods: {
    done (value) {
      const responseValues = { [this.prompt.key]: value }
      this.$emit('done', responseValues)
      this.$emit('change', responseValues)
    }
  }
}
</script>

