import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      showReschedulePrompt: false,
      isLoading: false,
      loadingComments: false
    }
  },
  props: ['appointment', 'comments', 'mode', 'clientId'],
  inject: ['time', 'userClient', 'commentsClient'],
  computed: {
    ...mapGetters(['session']),
    userId () {
      return this.clientId || this.session.id
    },
    date () {
      return new Date(this.appointment.date)
    },
    month () {
      return this.time.getShortMonth(this.appointment.date).toUpperCase()
    },
    day () {
      return this.time.getDayOfMonth(this.appointment.date)
    },
    dayOfWeek () {
      return this.time.getDayOfWeek(this.appointment.date).toUpperCase()
    },
    displayTime () {
      return this.time.formatTime(this.appointment.time)
    },
    hasComments () {
      return this.comments && this.comments.length
    },
    showDelete () {
      return this.mode === 'realtor' &&
        ['canceled', 'declined'].includes(this.appointment.status)
    },
    showCancel () {
      return this.mode === 'realtor'
        ? ['pending', 'scheduled'].includes(this.appointment.status)
        : this.appointment.status === 'scheduled'
    },
    showReschedule () {
      return this.mode !== 'realtor' && this.appointment.status === 'scheduled'
    },
    showGetFeedback () {
      return this.mode !== 'realtor' && this.appointment.status === 'completed' &&
        ((this.comments || [])[0] || { tags: [] }).tags.find(t => t.tag === 'FEEDBACK') === undefined
    }
  },
  methods: {
    deleteAppointment () {
      this.isLoading = true
      this.userClient.deleteAppointment(this.appointment)
        .then(r => {
          this.isLoading = false
          this.$emit('change', { ...this.appointment, status: 'deleted' })
        })
    },
    async submitComment (message, additionalTags) {
      this.loadingComments = true
      const tags = [{ type: 'reference', refType: 'appointment', refId: this.appointment.id }, ...(additionalTags || [])]
      const comment = await this.commentsClient.createComment({
        userId: this.clientId || this.session.id,
        authorId: this.session.id,
        message: message,
        tags
      })
      this.$emit('comment', comment)
      this.loadingComments = false
    },
    async requestReschedule (message) {
      this.showReschedulePrompt = false
      await this.updateAppointment(this.userId, { status: 'pending' })
      this.submitComment(`Reschedule${!message ? '' : ': '}${message}`)
    },
    async updateAppointment (values) {
      this.isLoading = true
      const self = this

      let updatedAppointment = { ...this.appointment, ...values }

      let diff = Object.keys(updatedAppointment).reduce((diff, key) => {
        if (this.appointment[key] === updatedAppointment[key]) return diff
        return { ...diff, [key]: updatedAppointment[key] }
      }, {})

      await this.userClient.updateAppointment(this.userId, updatedAppointment)

      self.isLoading = false
      this.$emit('change', updatedAppointment)

      const message = Object.keys(diff).map(k => `${k} to ${diff[k]}`).join(', ')
      this.submitComment(message)
    }
  }
}
