<template>
  <div class="flex-column">
    <title-label title="NEW CLIENT"></title-label>
    <form class="flex-one flex-column" @submit.prevent="createClient">
      <div class="flex-one scrolly">
        <label for="name" class="tertiary font-title font-2 m1-bottom">CLIENT NAME</label>
        <input class="input input-tertiary m2-bottom" name="name" v-model="clientName">

        <label for="email" class="tertiary font-title font-2 m1-bottom">CLIENT EMAIL</label>
        <input type="email" class="input input-tertiary m2-bottom" name="email" v-model="clientEmail">

        <label for="password" class="tertiary font-title font-2 m1-bottom">CLIENT ENTRY PASSWORD</label>
        <input class="input input-tertiary m2-bottom" name="password" v-model="clientPassword">

        <div class="flex-row m4-top m4-bottom">
          <div class="flex-one"></div>
          <input type="checkbox" name="isAdmin" v-model="isAdmin" class="m1-right">
          <label for="isAdmin" class="tertiary">Admin</label>
        </div>

        <div v-if="!isAdmin">
          <title-label title="PROPERTY" class="m2-top"></title-label>

          <label for="city" class="tertiary font-title font-2 m1-bottom">CITY</label>
          <input class="input input-tertiary m2-bottom" name="city" v-model="propertyCity">

          <label for="county" class="tertiary font-title font-2 m1-bottom">COUNTY</label>
          <input class="input input-tertiary m2-bottom" name="county" v-model="propertyCounty">

          <label for="describedAs" class="tertiary font-title font-2 m1-bottom">PROPERTY DESCRIBED AS</label>
          <input class="input input-tertiary m2-bottom" name="describedAs" v-model="propertyDescribedAs">

          <label for="date" class="tertiary font-title font-2 m1-bottom">PROPERTY DATE</label>
          <input class="input input-tertiary m2-bottom" name="date" v-model="propertyDate">
        </div>
      </div>

      <div class="p1-top">
        <button class="button alt h-fill border-primary" :disabled="submitDisabled">CREATE</button>
      </div>
    </form>
  </div>
</template>

<script>
import TitleLabel from '@/components/TitleLabel'

export default {
  name: 'CreateClient',
  data () {
    return {
      clientName: undefined,
      clientEmail: undefined,
      clientPassword: undefined,
      propertyCity: undefined,
      propertyCounty: undefined,
      propertyDescribedAs: undefined,
      propertyDate: undefined,
      isAdmin: false
    }
  },
  computed: {
    submitDisabled () {
      return !this.clientName || !this.clientEmail || !this.clientPassword
    }
  },
  components: { TitleLabel },
  inject: ['realtorClient'],
  methods: {
    createClient () {
      const self = this
      const property = { city: this.propertyCity, county: this.propertyCounty, state: 'CA', description: this.propertyDescribedAs, date: this.propertyDate }
      this.realtorClient.createClient(this.clientName, this.clientEmail, this.clientPassword, this.isAdmin, property)
        .then(res => {
          self.$emit('done')
        })
    }
  }
}
</script>
