import moment from 'moment'

export default {
  getMonth (date) {
    return moment(date, 'YYYY-MM-dd').format('MMMM')
  },
  getShortMonth (date) {
    return moment(date, 'YYYY-MM-dd').format('MMM')
  },
  getMonthDay (date) {
    return moment(date).format('MMM Do')
  },
  getDayOfMonth (date) {
    return moment(date, 'YYYY-MM-DD').format('Do')
  },
  getDayOfWeek (date) {
    return moment(date, 'YYYY-MM-dd').format('ddd')
  },
  formatTime (time) {
    return moment(time, 'H:mm').format('h:mm A')
  },
  formatFull (dateTime) {
    return moment(dateTime).format('MMM Do, h:mm A')
  }
}
