<template>
  <form @submit.prevent="done">
    <input type="text" :placeholder="placeholder" v-model="fieldValue" :maxlength="splitTextMax" autofocus="true" class="input input-tertiary" />
    <p class="text-right tertiary font-3 p1">{{charactersRemaining}}</p>
    <button v-if="showDoneButton" class="button alt h-fill m5-top" :disabled="!fieldValue">NEXT <i class="fas fa-arrow-right m1-left"></i></button>
  </form>
</template>

<script>
export default {
  name: 'SplitTextPrompt',
  data () {
    return {
      fieldValue: undefined
    }
  },
  props: ['prompt', 'responses'],
  computed: {
    placeholder () {
      return this.prompt.placeholder || 'answer here'
    },
    splitTextMax () {
      let l = 0
      this.prompt.keys.forEach(textPrompt => (l += (textPrompt.maxLength || 80)))
      return l
    },
    charactersRemaining () {
      return `${(this.fieldValue || '').length}/${this.splitTextMax}`
    },
    showDoneButton () {
      return !['GroupPrompt', 'OptionsPrompt'].includes(this.$parent.$options.name)
    }
  },
  watch: {
    fieldValue () {
      this.$emit('change', this.extractResponseValues())
    }
  },
  methods: {
    clear () {
      this.fieldValue = undefined
    },
    extractResponseValues () {
      let responseValues = {}
      let startIndex = 0
      const self = this
      for (const key in this.prompt.keys) {
        const k = this.prompt.keys[key]
        const end = startIndex + (k.maxLength || 80)
        const valuePart = self.fieldValue.slice(startIndex, end)
        responseValues[k.key] = valuePart
        startIndex += end
      }
      return responseValues
    },
    done () {
      this.$emit('done', this.extractResponseValues())
      this.fieldValue = undefined
    }
  },
  mounted () {
    this.prompt.keys.forEach(k => {
      this.fieldValue = (this.fieldValue || '') + (this.responses[k.key] || '')
    })
  }
}
</script>
