<template>
  <div class="flex-column v-fill">
    <div class="flex-row p3">
      <a v-if="showBack" class="tertiary font-title" @click="back"><i class="fas fa-arrow-left m1-right"></i>BACK</a>
      <span class="flex-one"></span>
      <a class="tertiary font-title" @click="$router.back()"><i class="fas fa-times m1-right"></i>CANCEL</a>
    </div>

    <transition name="fade-in">
      <div v-if="loadingState" class="flex-one flex-column flex-center align-center">
        <i class="fas fa-spinner fa-spin fa-3x"></i>
      </div>
    </transition>

    <transition name="fade-in">
      <div v-if="!downloadLink && !loadingState" class="prompt-wrapper p3 flex-one scrolly">
        <div class="prompt-prompt">
          <h2 class="app-font light font5 m3-bottom"><value-transition :value="currentPrompt.prompt" name="slide-left" /></h2>
        </div>

        <div class="scrolly prompt-component">
          <transition name="fade-in">
            <div :is="promptComponent" :prompt="currentPrompt" :responses="formResponses" @done="next" @change="responsesChange"></div>
          </transition>
        </div>
      </div>
    </transition>

    <p class="p1 tertiary text-right"><value-transition :value="progressLabel" name="fade-in" /></p>

    <div v-if="downloadLink" class="p3">
      <h2 class="tertiary app-font light font4 m4-bottom">Your form is ready to be reviewed.</h2>
      <button class="button alt" @click="downloadForm"><i class="fas fa-download m1-right"></i>Review TDS Form</button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import promptComponents from '@/components/prompts/prompt-components'

export default {
  name: 'Disclosures',
  data () {
    return {
      loadingState: false,
      formType: 'tds',
      formResponses: {},
      currentPromptIndex: 0,
      fieldValue: undefined,
      downloadLink: undefined
    }
  },
  computed: {
    ...mapGetters(['session']),
    prompts () {
      return require(`@/js/forms/${this.formType}.js`).default
    },
    currentPrompt () {
      return this.prompts[this.currentPromptIndex]
    },
    promptComponent () {
      return promptComponents[this.currentPrompt.type]
    },
    showBack () {
      return this.currentPromptIndex > 0
    },
    progressLabel () {
      return `${this.currentPromptIndex + 1} / ${this.prompts.length}`
    }
  },
  inject: ['userClient'],
  methods: {
    responsesChange (responseValues) {
      for (const key in responseValues) {
        this.$set(this.formResponses, key, responseValues[key])
      }
    },
    next (responseValues) {
      this.formResponses = Object.assign(this.formResponses, responseValues)
      if (this.currentPromptIndex === this.prompts.length - 1) {
        this.submit()
      } else {
        this.$el.querySelector('.prompt-wrapper').scrollTo(0, 0)
        this.currentPromptIndex++
        if (this.currentPrompt.condition) {
          if (this.currentPrompt.condition(this.formResponses) === false) {
            this.next()
          }
        }
      }
    },
    back () {
      this.currentPromptIndex--
      if (this.currentPrompt.condition) {
        if (this.currentPrompt.condition(this.formResponses) === false) {
          this.back()
        }
      }
    },
    saveProgress () {
      this.userClient.saveState(this.session.id, this.formType, { index: this.currentPromptIndex, responses: this.formResponses })
        .then(r => console.log('Progress saved'))
        .catch(e => console.log('Progress FAILED to saved', e))
    },
    submit () {
      this.userClient.submitForm(this.session.id, this.formType, this.formResponses)
        .then(() => this.$router.back())
    },
    downloadForm () {
      window.open(this.downloadLink, '_blank')
      this.$router.back()
    }
  },
  mounted () {
    this.formType = this.$route.query.form
    const prepopulateState = JSON.parse(decodeURIComponent(this.$route.query.state))
    this.formResponses = { ...this.formResponses, ...prepopulateState }

    this.loadingState = true
    this.userClient.getUserState(this.session.id, this.formType)
      .then(state => {
        this.formResponses = { ...this.formResponses, ...state.responses }
        this.currentPromptIndex = state.index || 0
        this.loadingState = false
      })
  },
  beforeDestroy () {
    this.saveProgress()
  }
}
</script>

<style lang="scss" scoped>
.prompt-prompt {
  min-height: 40px;
}
.prompt-component {
  min-height: 100px;
}
</style>
