const url = process.env.VUE_APP_USERS_URL

export default {
  async getProperty (userId) {
    return fetch(`${url}/users/${userId}/property`)
      .then(responseAsJson)
  },
  async createAppointment (clientId, date, time, status, label) {
    const options = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ date, time, status, label })
    }
    return fetch(`${url}/users/${clientId}/appointments`, options)
      .then(responseAsJson)
  },
  async getAppointments (clientId) {
    return fetch(`${url}/users/${clientId}/appointments`)
      .then(responseAsJson)
  },
  async updateAppointment (userId, appointment) {
    const options = {
      method: 'PATCH',
      headers: {
        'Content-Type': 'application/json',
        'Key': '12345'
      },
      body: JSON.stringify(appointment)
    }
    return fetch(`${url}/users/${userId}/appointments`, options)
      .then(responseAsJson)
  },
  async deleteAppointment (appointment) {
    const options = {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(appointment)
    }
    return fetch(`${url}/users/appointments`, options)
      .then(responseAsJson)
  },
  async getFiles (userId) {
    return fetch(`${url}/users/${userId}/files`)
      .then(responseAsJson)
  },
  getFileUrl (userId, key) {
    return `${url}/users/${userId}/files/${key}`
    // return fetch(`${url}/users/${userId}/files/url/${key}`)
    //   .then(responseAsJson)
  },
  async getUserState (userId, key) {
    return fetch(`${url}/users/${userId}/state/${key}`)
      .then(responseAsJson)
      .then(json => typeof json.value === 'object' ? json.value : {})
  },
  async saveState (userId, key, value) {
    const options = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ key, value })
    }
    return fetch(`${url}/users/${userId}/state`, options)
      .then(responseAsJson)
  },
  async submitForm (userId, formName, formResponses) {
    const options = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(formResponses)
    }
    return fetch(`${url}/users/${userId}/forms/${formName}`, options)
      .then(response => response.blob())
      .then(blob => URL.createObjectURL(blob))
      .then(url => {
        return url
      })
  }
}

function responseAsJson (response) {
  if (response.status >= 400) {
    throw new Error('Error')
  }
  return response.json()
}
